

































export default {
	data() {
		return {
			boxInfo:{}
		};
	},
	onLoad() {
		this.getBoxInfo();
	},
	methods: {
		getBoxInfo() {
			uni.showLoading({
				title:"正在加载数据"
			});
			var boxOfficeUrl="https://www.softeem.xin/maoyanApi/box/promovie/api/box/second.json";
			uni.request({
				url:boxOfficeUrl,
				success:res=>{
					this.boxInfo=res.data.data;
					
				},
				complete: () => {
					uni.hideLoading();
				}
			})
		}
	},
	onPullDownRefresh() {
		this.getBoxInfo();
		uni.stopPullDownRefresh();
	}
}
